.Gokublue{
    margin: 0;
    padding: 0;
    z-index: 0;
  }
  section{
    position: relative;
    width: 100%;
    height: 88vh;
    background: #3586ff;
    overflow: hidden;
    z-index: 0;
  }
  section .air{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background: url(https://1.bp.blogspot.com/-xQUc-TovqDk/XdxogmMqIRI/AAAAAAAACvI/AizpnE509UMGBcTiLJ58BC6iViPYGYQfQCLcBGAsYHQ/s1600/wave.png);
    background-size: 1000px 100px;
    z-index: 0;
    
  }
  section .air.air1{
    animation: wave 30s linear infinite;
    z-index: 1000;
    opacity: 1;
    animation-delay: 0s;
    bottom: 0;
    z-index: 0;
  }
  section .air.air2{
    animation: wave2 15s linear infinite;
    z-index: 999;
    opacity: 0.5;
    animation-delay: -5s;
    bottom: 10px;
    z-index: 0;
  }
  section .air.air3{
    animation: wave 30s linear infinite;
    z-index: 998;
    opacity: 0.2;
    animation-delay: -2s;
    bottom: 15px;
    z-index: 0;
  }
  section .air.air4{
    animation: wave2 5s linear infinite;
    z-index: 997;
    opacity: 0.7;
    animation-delay: -5s;
    bottom: 20px;
    z-index: 0;
  }
  @keyframes wave{
    0%{
      background-position-x: 0px; 
      z-index: 0;
    }
    100%{
      background-position-x: 1000px; 
      z-index: 0;
    }
  }
  @keyframes wave2{
    0%{
      background-position-x: 0px; 
      z-index: 0;
    }
    100%{
      background-position-x: -1000px; 
      z-index: 0;
    }
  }
  